.tabs-section{
	padding-top:80px;
	padding-bottom:80px;
     @include media-breakpoint-down(sm){
    padding-top:56px;
    padding-bottom:56px;
    }

    .tab-content{
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
    padding:30px;
    @include media-breakpoint-down(sm){
    padding:16px;
    }
    p{
    font-size:18px;
    line-height:30px;
    color: $color-grey1;
    }
    }
    
    .nav-tabs{
    gap:10px;
    border:none;
    li{
    margin-bottom:14px;
    @include media-breakpoint-down(sm){
    margin-bottom:30px;
    }
    }

     a{
     line-height:22px;
    background: #EEEEEE;
    border-radius: 20px;
    padding: 4px 14px 4px 14px;
    color:#626262;
    }
    a.active{
    color: #fff;
    background: #471F6F;
    box-shadow: 0px 0px 10px rgb(0 0 0 / 15%);
    }
    } 

}