.accordian-section{
    padding-top:61px;
    padding-bottom:112px;
    @include media-breakpoint-down(sm){
    padding-top:60px;
    padding-bottom:40px;
    }
  
    div#accordion.panel-group{
     p {
     margin-bottom: 15px;
    }
    }
    

    .panel{
    max-width: 100%;
    }

	h4{
	  background: #fff;
    max-width: 100%;
    padding: 23px;
    box-shadow: 0px 14px 33px rgba(0, 0, 0, 0.12);
    display: flex;
    text-align: left;
    align-items: center;
    justify-content: space-between;

    &:after{
    font-size: 40px;
    content: "\F107";
    font-family: "FontAwesome";
    float: right;
    width: 19px;
    color: #333333;
    }

    &:hover{
    cursor: pointer;
    color:$color-lightpurple;
    &:after{
    color:$color-lightpurple;
    }

    }

	}

	h4.collapsed{
	&:after{
	font-size: 40px;
    content: "\F105";
    font-family: "FontAwesome";
    float: right;
    width: 19px;
    color: #333333;
    }
        &:hover{
    cursor: pointer;
    color:$color-lightpurple;
    &:after{
    color:$color-lightpurple;
    }

    }
	}

	@include media-breakpoint-down(sm){
    h4{
    font-size:18px;
    padding: 15px 20px 15px 20px;
    &:after{
    font-size:32px;
    }
    }
    h4.collapsed{
    &:after{
    font-size:32px;
    }
    }
	}

	  .b-purple {
    border-left: 8px solid #8C2B73;
  }

  .b-teal {
    border-left: 8px solid #00998C;
  }

  .b-secondarygold {
    border-left: 8px solid #AD872D;
  }

  .b-blue {
    border-left: 8px solid #008CAA;
  }

}