.hero-banner-block {
  min-height: 310px;
  max-height: 520px;
  position: relative;


  .image {
    min-height: 310px;

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      max-height: 520px;
      min-height: 310px;
    }
  }

  .hero-banner-captions {
    left: 100px;
    right: 15%;
    position: absolute;
    bottom: 0;
    padding: 30px 30px 30px 0;
    max-width: 360px;
    width: 100%;
    background: rgba(0, 0, 0, .7);
    @include media-breakpoint-down(sm) {
      max-width: unset;
      left: 0;
      right: unset;
      padding: 20px;
    }

    &:before {
      content: "";
      background: rgba(0, 0, 0, .7);
      position: absolute;
      left: -100%;
      width: 100%;
      height: 100%;
      top: 0;
    }

    h1 {
      color: $white;
      font-size: 48px;
      line-height: 56px;
      font-family: $font-knockout;
      @include media-breakpoint-down(sm) {
        font-size: 32px;
      }
    }
  }
}