.featured-media{
 
    padding-bottom:121px;
    padding-top:121px;
    overflow:hidden;
    
    .wrapiframe{  
    iframe{
    display:none;
    width:100%;
    height:100%;
    position:absolute;
    }
    }

    .iframepadding{
    padding-bottom:57%;
    }
    
    img.playicon {
    position: absolute;
    bottom: 45%;
    left: 45%;
    }

    @media(min-width:1200px){
    .text-block{
    padding-left:121px;
    padding-right:121px;
    }
    }

    @media(max-width:1200px){
    .text-block{
    padding:20px 38px;
    }
    }

    h3{
    font-size:28px;
    font-weight: 100;
    margin-bottom:10px;
    line-height:33px;
    }

    p{
    font-size:20px;
    margin-bottom:20px;
    line-height:27px;
    a{
    &:hover{
    text-decoration: underline;
    }
    }
    }

	a.color-white{
	width: max-content;
    font-size: 20px;
    font-family: 'knockout';
    padding: 14px;
	text-transform: uppercase;
    &:hover{
    color:$white;
    border: 1px solid $white;
    padding: 13px;
    }
	}

}