footer {
  .tabs-columns{
  display:none
  }
  @media(max-width:575px){
  .tabs-columns{
  display:block;
  display:flex;
  justify-content:center;
  #accordion {
    width: 100%;
  }
  .smalltext.collapsed{
      margin-bottom: 0 !important;
    padding-top: 10px;
    padding-bottom: 10px;
    &:after{
    content: "\f107";
    font-family: 'Font Awesome 5 Free';
    float: right;
    background: #8080803d;
    border-radius: 22px;
    width: 19px;
    color: grey;
    }
  }
  .smalltext{
  margin-bottom: 0 !important;
    padding-top: 10px;
    padding-bottom: 10px;
    &:after{
    content: "\f106";
    font-family: 'Font Awesome 5 Free';
    float: right;
    background: #8080803d;
    border-radius: 22px;
    width: 19px;
    color: grey;
    }
  }
  .panel:last-child{
  border-bottom: 1px solid #80808075;
  }
  .panel {
   border-top: 1px solid #80808075;
    margin-bottom: 0 !important;
}

  }
  .default-columns{
   display:none;
   }
  }
  a {
    color: $color-purple;
  }
  .socialicons{
    a{
      &:hover{
        color: $color-purple;
      }
    }
    @media(max-width:575px){
    a{
    .fab{
    font-size: 22px;
    padding: 7px;
    }
    }
    }
  }
  a.h4 {
    color: $color-purple;
    &:hover{
      text-decoration: underline;
    }
  }
  @media(max-width:575px){
  a.h4 {
    background: $color-purple;
    color:#fff;
  }
  }
  .footer-menu-columns{
    a{
      &:hover {
        text-decoration: underline;
      }
    }
  }
  .ctas {
    gap: 21px;

    li {
      border-right: 2px solid #c4c4c4;
      padding-right: 21px;
    }
    @media(max-width:575px){
     li {
    border: none;
    background: #471F6F;
    margin: 10px 0px;
    padding: 10px 0;
}
    }


    li:last-child {
      border: none;
      padding-right: 0;
    }
  }

  .footer-menu-columns {
    .smalltext {
      font-weight: 700;
      color: $color-grey1;
      line-height: 19.07px;
    }
  }

  .copyright {

    background: #ebebeb;
    a{
      text-decoration: underline;
    }
  }
  @media(max-width:575px){
  .copyright{
  line-height: 30px;
  }
  }
}

.footer-border {
  background: linear-gradient(
                  90deg, #34194D 0%, #8C2B73 100%);
  height: 30px;
}