.wysiwyg-section{
	padding-top:80px;
	padding-bottom:80px;
	@include media-breakpoint-down(sm){
	padding-top:56px;
	padding-bottom:56px;
	}

	 p{
	font-size: 18px;
	line-height: 30px;
	color: #333333;
	@include media-breakpoint-down(sm){
	font-size: 16px;
	}
	 }
	 h1,h2,h3{
	 font-size: 28px;
     line-height: 33px;
	color:$color-purple;
	margin-bottom:30px;
	@include media-breakpoint-down(sm){
	margin-bottom:25px;
	font-size: 24px;
    line-height: 28px;
	}
	 }

}