.collage-image-section {
  background: #F6F6F6;
  background-repeat: no-repeat;
  background-position: right;
  background-size: 19%;

  .collage-image {
    overflow: hidden;
    border-bottom: 3px solid #f6f6f6;
  }

  .collage-image:nth-child(2) img:last-child {
    height: 100%;
    object-fit: cover;
    overflow: hidden;
  }

  .collage-image:nth-child(1) img:last-child {
    height: 45%;
    object-fit: cover;
    overflow: hidden;
  }

  .collage-image > img {
    width: 100%;
    border: 3px solid #F6F6F6;

  }

  .collage-image-box {
    background: $white;
    height: auto;
    border-bottom: 8px solid $color-blue;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
    width: 98%;

    h2 {
      margin-bottom: 10px;
    }

    p {
      margin-bottom: 42px;
      line-height: 21px;
    }

    a {
      padding-bottom: 8px;
      display: flex;
    }
  }

  @include media-breakpoint-down(md) {
    .collage-image-box {
      padding: 15px;
      margin: 3px;
    }
  }
  @include media-breakpoint-up(md) {
    .collage-image-box {
      position: absolute;
      padding: 30px;
      top: 124px;
      left: -31%;


    }
  }
  @media(max-width: 990px) and (min-width: 768px) {
    .collage-image-box {
      position: absolute;
      padding: 15px;
      top: 34px;
      left: -25%;

      p {
        margin-bottom: 8px;
      }

      a {
        padding-bottom: 0;
      }

      h2 {
        font-size: 24px;
        line-height: 30px;
      }

    }
  }

}

@include media-breakpoint-down(sm) {
  .collage-image-section {
    background: none !important;
  }
}
