.sidebar-cta-buttons {
  padding-top:40px;
  max-width: 230px;
  @include media-breakpoint-down(sm) {
    max-width: unset;
  }

  ul {
    padding: 0;
    li {
      list-style-type: none;
      width: 100%;
      display: flex;
      text-align: center;
      margin-bottom: 10px;

      a {
        text-transform: uppercase;
        color: $white;
        padding: 20px 20px;
        width: 100%;
        font-size: 19px;
        line-height: 22px;
        text-decoration: none;
        font-family: $font-knockout;
        position: relative;
        @include media-breakpoint-down(sm){
          font-size: 16px;
          line-height: 22px;

        }

        &:after {
          content: "\f061";
          font-family: 'Font Awesome 5 Free';
          top: 50%;
          position: absolute;
          right: 15px;
          transform: translate(0, -50%);
        }
        i{
        padding-left:8px;
        }
      }

      &:first-child {
        a {
          background: #34194D;
        }
      }

      &:nth-child(2) {
        a {
          background: $color-teal;
        }
      }

      &:nth-child(3) {
        a {
          background: $color-lightgold;
          color: $color-purple;

          &:after {
            color: $color-purple;
          }
        }
      }

      &:nth-child(4) {
        a {
          background: $color-blue;
        }
      }
    }
  }
}