.fullwidthslideshowsection {
  @include media-breakpoint-up(md) {
    padding-top: 100px;
    padding-bottom: 100px;
  }
  @include media-breakpoint-down(md) {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  h1 {
    margin-bottom: 30px;
    @include media-breakpoint-down(sm){
    font-size: 32px;
    line-height: 37px;
    margin-bottom: 25px;
    }
  }

  .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(71, 31, 111, 0.7), rgba(71, 31, 111, 0.7));
    bottom: 7px;
  }

  .slick-slide {
    .header-text {
      display: none;
    }
  }

  .slick-current {

    .header-text {
      display: block;
      .caption{
      font-weight: 600;
      }
    }

    .overlay {
      display: none;
    }
  }

  .header-text {
    text-align: left;
    background: #fff;
    position: absolute;
    bottom: 21px;
    right: 17px;
    max-width: 400px;
    padding: 17px 20px;
    border-bottom: 8px solid $color-blue;
  }

  @include media-breakpoint-down(sm) {
    .header-text {
      bottom: 21px;
      right: 17px;
      width: 100%;
      padding-left: 12px;
      padding-top: 10px;
      padding-bottom: 10px;
      opacity: 1;

      
    }
  }
  @media(max-width:500px){
  .header-text {
      position:relative;
      bottom: 0px;
      right: 0px;
      max-width: 100%;
      padding-left: 12px;
      padding-top: 10px;
      padding-bottom: 10px;
      opacity: 1;

      
    }
    .slick-prev {
    top:38%;
  }
  .slick-next {
    top:38%;
  }
  }

  @media(max-width:320px){
    .slick-prev {
    top:32%;
  }
  .slick-next {
    top:32%;
  }
  }

  .slick-prev {
    left: 52px;
    z-index: 99999;
  }

  .slick-prev {
    &::before {
      color: #fff !important;
      opacity: 1;
      content: "\f104";
      font-size: 60px;
      font-family: 'FontAwesome';
    }

    @include media-breakpoint-down(sm) {
      &::before {
        font-size: 25px;
      }
    }
  }

  .slick-next {
    right: 52px;
    z-index: 99999;
  }

  .slick-next {
    &::before {
      color: #fff !important;
      opacity: 1;
      content: "\f105";
      font-size: 60px;
      font-family: 'FontAwesome';
    }

    @include media-breakpoint-down(sm) {
      &::before {
        font-size: 25px;
      }
    }
  }

  @include media-breakpoint-up(sm) {
    .slick-list.draggable {
      padding: 0 123px !important;
    }
  }
  @include media-breakpoint-down(sm) {
    .slick-arrow{
    display:none !important;
    }
    .slick-prev {
      left: 0px;
    }

    .slick-next {
      right: 0px;
    }
    .slick-list.draggable {
      padding: 0 26px !important;
    }
  }

  .slick-slide {
    margin: 0px 8px;
    position: relative;

    &::before {
      position: absolute;
      content: "";
      height: 25px;
      width: 100%;
      background: $white;
      z-index: 9;
      top: 0;
      right: 0;
    }

    &::after {
      position: absolute;
      content: "";
      height: 25px;
      width: 100%;
      background: $white;
      z-index: 9;
      bottom: 0;
      right: 0;
    }

  }

  .slick-slide img {
    width: 100%;
  }

  .slick-prev:before,
  .slick-next:before {
    color: black;
  }


  .slick-slide {
    transition: all ease-in-out .3s;
    opacity: 1;
  }

  .slick-active {
    opacity: 1;
  }

  .slick-current {
    opacity: 1;

    &::after {
      content: none;
    }

    &::before {
      content: none;
    }
  }


     .slick-dots {
      bottom: -40px;

      li {
      margin:0 4px;
        button {
          &:before {
            color: #333333;
            font-size: 13px;
            opacity: 1;
          }
        }

        &.slick-active {
          button {
            &:before {
              color: transparent;
              border: 2px solid #333;
              opacity: 1;
              font-size: 13px;
              width: 11px;
              height: 11px;
              border-radius: 30px;
              top: 3px;
              left: 5px;
            }
          }
        }
      }
    }

}