//top header
.main-container{
  margin-top: 80px;
}
@include media-breakpoint-down(lg) {
.main-container {
    margin-top: 53px;
}
}
body.logged-in{
  header{
  top:31px;
  }
 @include media-breakpoint-down(lg) {
header.mobilenav{
  top:34px;
  }
}
  @include media-breakpoint-down(md) {
.main-container {
    margin-top: 38px;
}
 header.mobilenav{
  top:48px;
  }
}
}

.top-nav-bar {
  ul.top-header__menu__list {
    li.menu-item {
      a {
        font-size: 14px;
        line-height: 19.07px;
        color: #828282;
        font-weight: 700;
        margin: 0 10px;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
}

header {
  position: fixed;
  width: 100%;
  z-index: 999;
  top: 0;

  #mnmwp-main-menu{
  background:none;
  margin-right:0;
  ul.sub-menu {
    background: #fff;
}
  }
#mnmwp-main-menu > ul > li:hover > a {
    color: #828282;
}
#mnmwp-main-menu ul.sub-menu {
    background: #fff;
    border-top: 7px solid #471F6F;
    box-shadow: 1px 1px 1px 1px #80808066;
}
#mnmwp-main-menu li:hover > ul > li a:hover {
    background: #471F6F;
    color: #fff;
}
#mnmwp-main-menu li:hover > ul > li a {
    background: #fff;
    color: #471F6F;
}
#mnmwp-main-menu li:hover > ul > li a {
    border: none;
}


  #mnmwp-main-menu ul ul > li:hover > a {
    background: #fff;
    color: #828282;
}
#mnmwp-main-menu ul ul li a {
    background: #fff;
    color: #828282;
}
#mnmwp-main-menu ul ul ul li a {
    background: #fff;
    color: #828282;
}
#mnmwp-main-menu ul ul ul li a {
    background: #fff;
    color: #828282;
}
#mnmwp-main-menu ul ul ul li:hover > a {
    background: #fff;
    color: #828282;
}
#mnmwp-main-menu ul li a:hover {
    color: #471F6F;
}
#mnmwp-main-menu > ul > li > a:hover {
    color: #471F6F !important;
}
#mnmwp-main-menu > ul > li > a::before {
    display:none !important;
}
#mnmwp-main-menu > ul > li > a::after {
    display:none !important;
}
#mnmwp-main-menu ul ul li.has-sub > a:after, #mnmwp-main-menu ul ul li.has-sub > a:before {
    background: #471F6F;
}
#mnmwp-main-menu ul > li.has-sub > a:after, #mnmwp-main-menu ul > li.has-sub > a:before{
height:0;
width:0;
}
#mnmwp-main-menu ul ul li.has-sub > a:before {
    background: #471F6F;
    content: "\f105";
    font-family: 'Font Awesome 5 Free';
    position: absolute;
    top: 7px !important;
    transform: none;
}
#mnmwp-main-menu > ul > li.has-sub > ul.sub-menu li.has-sub:hover > a:before {
    content: "\f105";
    font-family: 'Font Awesome 5 Free';
    position: absolute;
    top: 7px !important;
    transform: none;
}
#mnmwp-main-menu li:hover > ul > li {
    height: auto;
}
#mnmwp-main-menu li:hover > ul > li a {
    border: none;
    padding: 7px 0 !important;
}
#mnmwp-main-menu ul ul ul {
    border-top: 7px solid #008CAA !important;
}
#mnmwp-main-menu ul ul ul ul {
    border-top: 7px solid #AD872D !important;
}
  #mnmwp-main-menu ul li a {
    font-size: 14px;
    line-height: 19.07px;
    color: #828282;
    font-weight: 700;
    margin: 0;
    padding: 4px;
    letter-spacing: inherit;
    text-align: center;
}
  #mnmwp-main-menu ul li a:hover {
    color: #828282;
}
  #mnmwp-main-menu > ul > li:hover {
    background: #fff;
}
  #mnmwp-main-menu > ul > li{
  background:none;
  ::hover{
  background:none;
  }
  }
  #mnmwp-main-menu > ul > li.has-sub > a {
    padding-right: 0;
}
#mnmwp-main-menu a {
    text-transform: capitalize !important;
        padding: 3px 10px !important;
}

  .mobilenav{
    img{
      width: 93px;
    }
  }
  background: #fff;

  a.branding:after {
    content: "";
    position: absolute;
    right: -16px;
    top: 0;
    bottom: 0;
    margin-top: 10px;
    margin-bottom: 10px;
    border-right: 1px solid $color-border-light;
  }

  a.branding {
    display: block;
    position: relative;
    margin-right: 32px;
  }

  .quicklinks-nav-bar {
    font-family: 'knockout';
    border-left: 2px solid #c4c4c4;
    padding-left: 10px;

    button {
      color: $color-purple;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.main-menu-nav-bar {
  a.nav-link.h4:hover {
    color: $color-purple;
    border-bottom: 5px solid $color-purple;
    margin-bottom: 0px;
    padding-bottom: 0px;
    margin: 12px 12px 0px 12px;
  }

  a.nav-link.h4:active {
    color: $color-purple;
    border-bottom: 5px solid $color-purple;
  }

  a.nav-link.h4 {
    font-size: 20px;
    color: $color-grey1;
    line-height: 23.44px;
    font-family: $font-knockout;
    text-transform: uppercase;
    font-weight: 600;
    padding: 5px 0px;
    margin: 12px 12px 0px 12px;
  }

  i.fa.fa-search {
    margin-top: 21px;
  }
}

