.campus-locations {
  background-image: url(/wp-content/uploads/2021/11/Mask-Group.png);
  background-repeat: no-repeat;
  background-position: 164px 0px;
  padding-top: 100px;
  padding-bottom: 100px;
  @include media-breakpoint-down(md) {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  h1{
    margin-bottom: 30px;
  }

  @include media-breakpoint-up(sm) {
    .card {
      .morelink {
        position: absolute;
        bottom: 27px;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    .card {
      .morelink {
        position: absolute;
        bottom: 27px;
      }
    }
  }



  .card {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
    text-align: center;
    padding-bottom: 48px;
    h3 {
      margin-bottom: 8px;
    }
    .icon{
    width:86px;
    }
    .address{
    margin-bottom:8px;
    }
    .links{
        gap: 10px;
    margin-top: 40px;
    margin-bottom: 24px;
    width: 100%;
    padding-left: 40px;
    padding-right: 40px;
    a{
    font-size:20px;
    font-family:knockout;
    text-transform:uppercase;
    line-height:23px;
    background:#34194D;
    color:#fff;
    padding-top: 17px;
    padding-bottom: 17px;
    }
    }
    .iconbox {
    min-height: 144px;
    }
  }

  .b-purple {
    border-bottom: 8px solid #8C2B73;
  }

  .text-purple {
    color: #8C2B73;
  }

  .b-teal {
    border-bottom: 8px solid #00998C;
  }

  .text-teal {
    color: #00998C;
  }

  .b-secondarygold {
    border-bottom: 8px solid #AD872D;
  }

  .text-secondarygold {
    color: #AD872D;
  }

  .b-blue {
    border-bottom: 8px solid #008CAA;
  }

  .text-blue {
    color: #008CAA;
  }


}