.blockquote-section{
     
     padding-top:80px;
     padding-bottom:80px;

     @include media-breakpoint-down(md){
     padding-bottom:56px;
     padding-top:56px;
     }

	.blockquote-content{
	border-bottom:7px solid $color-lightpurple;
	padding:30px;
	padding-left: 51px;
	filter: drop-shadow(0px 14px 33px rgba(0, 0, 0, 0.12));
	.open-sans-26{
	color:$color-grey1;
	font-style: italic;
    font-weight: normal;
	}
	img.quote-icon{
	position: absolute;
    left: -42px;
	}

    @include media-breakpoint-down(md){
    .block-body{
    font-size: 24px;
    line-height: 36px;
    }
    }
    p{
    color:$color-grey1;
    }
	}
    
    @include media-breakpoint-down(md){
    .blockquote-content{
    padding:22px;
    padding-top: 15px;
    padding-bottom: 15px;
    border-bottom:7px solid $color-lightpurple;
    img.quote-icon{
	position: absolute;
    left: -12px;
    width: 28px;
    top: -9px;
	}
    }
    }

}