// Paths
//$svg-path: "../svgs";
$image-path: "../images";
$font-path: "../fonts";


/**** Override Bootstrap Variables ****/
// node_modules/bootstrap/scss/_variables.scss
$grid-breakpoints: (
        xs: 0,
        sm: 576px,
        md: 768px,
        lg: 992px,
        xl: 1200px
);

$container-max-widths: (
        sm: 540px,
        md: 720px,
        lg: 960px,
        xl: 1140px
);

// Grid columns
// Set the number of columns and specify the width of the gutters.
$grid-columns: 12;
$grid-gutter-width: 30px;
$grid-row-columns: 6;


