.fullwidthmediasection {
  &::after {
    content: "";
    height: 100px;
    width: 4px;
    background: $color-lightgold;
    position: absolute;
    left: 50%;
    margin-top: -50px;
  }

  .carousel-caption {
    background: $white;
    color: $black;
    border-bottom: 8px solid $color-lightpurple;
  }

  @include media-breakpoint-up(lg) {
    .carousel-caption {
      bottom: 70px;

      h2 {
        margin-bottom: 10px;
      }

      p {
        margin-bottom: 42px;
      }
    }
  }
  @include media-breakpoint-up(md) {
    .carousel-caption {
      padding: 30px;
      text-align: left;
      left: 100px;
      max-width: 400px;
    }
  }
  @include media-breakpoint-down(md) {
    .carousel-caption {
      padding: 10px;
      text-align: left;
      bottom: 70px;
      max-width: 400px;
    }
  }
  @include media-breakpoint-down(sm) {
    .carousel-caption {
      padding: 8px;
      text-align: left;
      bottom: 10px;
      left: 3%;
      right: 3%;
      max-width: 400px;
    }
  }
  .carousel-inner{
      
    .carousel-item{
    min-height: 700px;
       img{
           height: 100%;
           object-fit: cover;
           min-height: 700px;
       }
    }
  }
}
