.heroslider {

  .shadow {
    position: absolute;
    width: 100%;
    height: 100%;
    box-shadow: inset 0px -100px 153px 0px #000000 !important;
    border-radius: 20px;
    top: 0;
    left: 0;
  }

  @include media-breakpoint-up(md) {
    .carousel-caption {
      width: 457px;
      left: 5%;
    }
  }
  @include media-breakpoint-up(md) {
    .carousel-caption {
      bottom: 80px;

      h1 {
        margin-bottom: 8px;
      }

      p {
        margin-bottom: 16px;
      }
    }
  }
  @include media-breakpoint-down(sm) {
    .carousel-caption {
      .morelink {
        font-size: 10px;
        line-height: 13px;
      }
    }
  }


  .carousel-item {
    @include media-breakpoint-down(sm) {
      .carousel-caption {
        bottom: 0px;
        @include media-breakpoint-down(sm) {
          h1 {
            font-size: 20px;
            line-height: 28px;
          }
        }
        @include media-breakpoint-up(sm) {
          h1 {
            font-size: 30px;
            line-height: 38px;
          }
        }
        @include media-breakpoint-up(md) {
          h1 {
            font-size: 48px;
            line-height: 56px;
          }
        }
      }
    }

  }

  .carousel-control-prev-icon {
    visibility: hidden;
  }

  .carousel-control-next-icon {
    visibility: hidden;
  }

  .carousel-control-next-icon {
    &::before {
      content: "\f105";
      font-size: 60px;
      font-family: 'FontAwesome';
      color: #fff;
      opacity: 1;
      visibility: visible;
    }
  }

  @include media-breakpoint-down(md) {
    .carousel-control-next-icon {
      &::before {
        font-size: 25px;
      }
    }
  }

  .carousel-control-prev-icon {
    &::before {
      content: "\f104";
      font-size: 60px;
      font-family: 'FontAwesome';
      color: #fff;
      opacity: 1;
      visibility: visible;
    }
  }

  @include media-breakpoint-down(md) {
    .carousel-control-prev-icon {
      &::before {
        font-size: 25px;
      }
    }
  }

  .carousel-control-prev, .carousel-control-next {
    opacity: 1;
    width: 4%;
  }
}