.sidebar-contact-block {
  max-width: 230px;
  padding-top:20px;
  padding-bottom:20px;
  h5 {
    font-family: $font-open;
    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    color: $color-purple;
    margin:0 0 15px;
  }

  ul {
    padding: 0;
    border-bottom: 2px solid #C4C4C4;
    margin-bottom: 40px;

    li {
      align-items: baseline;
      list-style-type: none;
      margin-bottom: 15px;
      display: flex;
      flex-flow: row wrap;

      i {
        font-size: 16px;
        width: 10px;
        line-height:normal;
        text-align: center;
        margin-right: 20px;
        color: $color-purple;
      }

      a {
        font-size: 16px;
        color: #282B2C;
        line-height: 22px;
        font-weight: normal;
        position: relative;
        text-decoration: none;

        span {
          display: block;
        }
      }
    }
  }
}