.testimonial-carousel-section {
  padding-bottom: 100px;
  padding-top: 100px;

  .slider-for {
    .slick-slide {
      margin: 0 auto;
      text-align: center;

      .active-image {

        position: relative;
        width: 300px !important;
        height: 300px;
        @include media-breakpoint-down(md){
        margin-bottom: 30px;
        }

        img {
          width: 100%;
          height: 100%;
          border-radius: 300px;
          object-fit: cover;
          padding:15px;
        }
      
       &:before{
    content: "";
    background: url(/wp-content/themes/palmer/dist/images/border-dotted.svg);
    position: absolute;
    left: 0;
    top: 0px;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: contain;
       }

      }
    }
  }

  .slider-content {
    .author {
      color: $color-grey1;
      margin-bottom: 40px;
       @include media-breakpoint-down(md){
        font-size: 16px !important;
        margin-bottom: 10px;
      }
    }

    .quote {
      font-size: 26px;
      line-height: 30px;
      font-family: $font-knockout;
      margin-bottom: 30px;
      color: $color-grey1;

      a {
        color: $color-purple;
        text-decoration: underline;
      }
    }
  }


  .slider-nav {
    .slick-slide {
      max-width: 79px;
      margin-right: 23px;
      @include media-breakpoint-down(sm) {
        max-width: 48px;
        margin-right: 20px;
      }

      .dot-images {
        width: 80px !important;
        height: 80px;
        position: relative;

        @include media-breakpoint-down(sm) {
          width: 48px !important;
          height: 48px;
        }

        img {
          border-radius: 80px;
          height: 100%;
          width: 100%;

          &:hover {
            cursor: pointer;
          }
        }
      }

      &.slick-current {
        .dot-images {
          &:before {
            content: "";
            background: linear-gradient(
                            0deg, rgba(0, 153, 140, 0.5), rgba(0, 153, 140, 0.5));
            position: absolute;
            width: 100%;
            height: 100%;
            border-radius: 100px;
            z-index: 9;
          }
        }
      }
    }

    .slick-track {
      width: 100% !important;
    }

    .slick-list {
      padding: 0 !important;
    }
  }

  .slick-slide {
    transition: all ease-in-out .3s;
    opacity: 1;

    img {
      width: 100%;
    }
  }

  .slick-active {
    opacity: 1;
  }

  .slick-current {
    opacity: 1;
  }
}