.image-gallery-section {
  padding-top: 100px;
  padding-bottom: 100px;

  .slick-current {
    .black-overlay {
      display: none;
    }
  }

  .slick-slide {
    margin: 0 20px;
  }

  .slick-slide img {
    width: 100%;
  }

  .slick-prev:before,
  .slick-next:before {
    color: black;
  }

  .slick-slide {
    margin: 0;
    transition: all ease-in-out .3s;
    opacity: 1;

    &:first-child, &:nth-child(5n+5) {
      .header-text {
        border-color: $color-darkpurple;
      }
    }

    &:nth-child(2), &:nth-child(6n+6) {
      .header-text {
        border-color: $color-green;
      }
    }

    &:nth-child(3), &:nth-child(7n+7) {
      .header-text {
        border-color: $color-lightgold;
      }
    }

    &:nth-child(4), &:nth-child(8n+8) {
      .header-text {
        border-color: $color-blue;
      }
    }
  }

  .slick-active {
    opacity: 1;
  }

  .slick-current {
    opacity: 1;
  }

  .header-text {
    text-align: left;
    background: #fff;
    position: absolute;
    bottom: 20px;
    right: 20px;
    max-width: 400px;
    padding: 17px 20px;
    border-bottom: 8px solid;

    @include media-breakpoint-down(sm) {
      bottom: 0;
      right: unset;
      width: 100%;
      padding: 10px 12px;
      opacity: 1;
      max-width: unset;
      border: none;
    }
    .slidecount {
    font-size: 14px;
    line-height: 19px;
}
@include media-breakpoint-down(sm){
.content{
font-weight:600;
  }
}

}
}


.image-gallery-section {
  .image-gallery {
    max-height: 407px;
    overflow: hidden;
    margin-bottom: 3px;
    @include media-breakpoint-down(sm) {
      overflow: visible;
    }

    .slick-list {
      @include media-breakpoint-down(sm) {
        margin-bottom: 20px;
      }

      .slick-track {

        .slick-slide {
          &:first-child, &:nth-child(5n+5) {
            border-bottom: 6px solid $color-darkpurple;
          }

          &:nth-child(2), &:nth-child(6n+6) {
            border-bottom: 6px solid $color-green;
          }

          &:nth-child(3), &:nth-child(7n+7) {
            border-bottom: 6px solid $color-lightgold;
          }

          &:nth-child(4), &:nth-child(8n+8) {
            border-bottom: 6px solid $color-blue;
          }

          .slide-main {
            max-height: 407px;

            img {
              height: 407px;
              object-fit: cover;
            }
          }
        }
      }
    }

    .slick-dots {
      bottom: -53px;

      li {
      margin:0 4px;
        button {
          &:before {
            color: #333333;
            font-size: 13px;
            opacity: 1;
          }
        }

        &.slick-active {
          button {
            &:before {
              color: transparent;
              border: 2px solid #333;
              opacity: 1;
              font-size: 13px;
              width: 11px;
              height: 11px;
              border-radius: 30px;
              top: 3px;
              left: 5px;
            }
          }
        }
      }
    }
  }

  .image-gallery-nav {
    .slick-list {
      height: 66px;
      z-index: -1;

      .slick-track {
        .slick-slide {
          margin: 0 1px;
          position: relative;

          &:before {
            content: "";
            background: rgba(0, 0, 0, .7);
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
          }

          &.slick-current {
            &:before {
              background: none;
            }

          }

          .slide-nav {
            height: 66px;

            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }
        }
      }
    }

    .slick-prev {
      left: 20px;

      &:before {
        content: "\f053";
        font-family: 'Font Awesome 5 Free';
        font-weight: bold;
        color: $white !important;
      }
    }

    .slick-next {
      right: 20px;

      &:before {
        content: "\f054";
        font-family: 'Font Awesome 5 Free';
        font-weight: bold;
        color: $white !important;
      }
    }
  }
}