.token-grid-section {
  padding-top: 100px;
  padding-bottom: 100px;

  .token-grid-title {
    font-family: $font-knockout;
    margin-bottom: 36px;
    font-size: 28px;
    line-height: 33px;
     @include media-breakpoint-down(sm){
  font-size: 24px;
  line-height: 28px;
  }
  }
  
  .caption{
  font-size: 18px;
  line-height: 30px;
  @include media-breakpoint-down(sm){
  font-size: 16px;
  line-height: 22px;
  }
  color: $color-grey1;
  width:67%;
  }

  .token{
  margin-bottom:40px;
  &:last-child{
  margin-bottom:0;
  }
  }

   p:nth-child(1) {
    margin-bottom: 24px;
  }

  .icon-wrap {

  .token-title{
  margin-bottom:5px;
  font-family: Knockout;
  font-size: 21px;
  line-height: 30px;
  color: $color-grey1;
  }
    
    .number{
    width: 80px;
    height: 80px;
    margin-bottom:20px;
    background: $color-lightgold;
    padding: 28px;
    border-radius: 44px;
    font-size: 28px;
    line-height: 30px;
    }

    i{
    width: 80px;
    height: 80px;
    margin-bottom:20px;
    background: $color-lightgold;
    padding: 28px;
    color: $color-purple;
    border-radius: 44px;
    font-size: 24px;
    line-height: 28px;
    }

   
    img {
      height: 58px;
      padding: 10px;
    }
  }

  .buttons_wrap {
    margin-bottom: 80px;
  }
}