.quicklinksbar {
  width: 90%;
  margin: 0 auto;
  filter: drop-shadow(0px 0 91px rgba(0, 0, 0, 1.25));
  overflow: hidden;
  margin-top: -73px;
  @include media-breakpoint-down(md) {
    margin-top: 0px;
  }

  .quick-links-left {
    background-color: $color-lightgold;
    padding: 33px 41px;

    h4 {
      margin-bottom: 8px;
    }

    p {
      color: $color-grey1;
    }

    &::after {
      content: "";
      border-top: 202px solid $color-lightgold;
      border-right: 36px solid $white;
      position: absolute;
      left: 100%;
      top: 0;
      z-index: 9999;
    }
  }

  .quick-links-right {
    background-color: $white;
    text-align: center;
    gap: 16px;

    a {
      background: $color-darkpurple;
      font-family: Knockout;
      font-size: 19px;
      line-height: 22px;
      text-transform: uppercase;
      border: none;
      padding: 20px 30px;

      &:hover {
        color: $white;
        border: 2px solid $color-lightgold;
        padding: 18px 28px;
      }

      button {
        text-transform: uppercase;
      }
    }

    @include media-breakpoint-down(sm) {
      a {
        margin: 1px;
      }
    }
    @include media-breakpoint-down(lg) {
      a {
        padding: 14px 14px;
      }
    }

  }


  .quick-links-icon {
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 21px;
    text-align: center;
    color: #333333;
    border: 2px solid #333333;
    border-radius: 50%;
    padding: 10px;
    position: absolute;
    right: 8%;
    bottom: 20%;
  }

  .quick-links-ml {
    margin-left: 8px;
  }
}
