.flexible-ctablocks {
  padding-top: 100px;
  padding-bottom: 100px;
  @include media-breakpoint-down(md) {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  h1{
    margin-bottom: 50px;
  }

  @include media-breakpoint-up(sm) {
    .card {
      height: 400px;

      .morelink {
        position: absolute;
        bottom: 27px;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    .card {
      height: 360px;

      .morelink {
        position: absolute;
        bottom: 27px;
      }
    }
  }

  @media(max-width: 1200px) and (min-width: 990px) {
    .card {
      height: 462px;
    }
  }

  .card {
    box-shadow: 0px 2px 8px rgb(0 0 0 / 15%);
    padding: 30px 20px;

    h3 {
      margin-bottom: 10px;
    }
  }

  .b-purple {
    border-bottom: 8px solid #8C2B73;
  }

  .text-purple {
    color: #8C2B73;
  }

  .b-teal {
    border-bottom: 8px solid #00998C;
  }

  .text-teal {
    color: #00998C;
  }

  .b-secondarygold {
    border-bottom: 8px solid #AD872D;
  }

  .text-secondarygold {
    color: #AD872D;
  }

  .b-blue {
    border-bottom: 8px solid #008CAA;
  }

  .text-blue {
    color: #008CAA;
  }


}